import React from 'react';
import './project.scss';

import LinkIcon from '@material-ui/icons/Link';

import Image1 from '../img/adabse243awada.png'
import Image2 from '../img/oonda932bi1b.png'
import Image3 from '../img/zcerwwr342aw24.png'

function Project() {
    return (
        <div className="project-container">
            <div className="project-title">
                <h1><span>My</span> Works</h1>
            </div>
            <div className="project-content">
                <div className="project__box-content">
                    <div className="project__box-image">
                        <img src={Image1} alt="box" />
                    </div>
                    <div className="project__box-overlay">
                        <a href="http://" target="_blank" rel="noopener noreferrer">
                            <LinkIcon />
                        </a>
                    </div>
                </div>
                <div className="project__box-content">
                    <div className="project__box-image">
                        <img src={Image2} alt="box" />
                    </div>
                    <div className="project__box-overlay">
                        <a href="http://" target="_blank" rel="noopener noreferrer">
                            <LinkIcon />
                        </a>
                    </div>
                </div>
                <div className="project__box-content">
                    <div className="project__box-image">
                        <img src={Image3} alt="box" />
                    </div>
                    <div className="project__box-overlay">
                        <a href="http://" target="_blank" rel="noopener noreferrer">
                            <LinkIcon />
                        </a>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Project;
