import React from 'react';
import './navbar-top.scss';

import AllOutOutlinedIcon from '@material-ui/icons/AllOutOutlined';

import { Link } from 'react-scroll'

function NavbarTop() {
    return (
        <div className="navbar">
            <div className="navbar__logo">
                <AllOutOutlinedIcon />
            </div>
            <div className="navbar__list">
                <div className="navbar__list-items"><Link activeClass="active" isDynamic={true} to="about" spy={true} smooth={true} offset={50} duration={500}>About</Link></div>
                <div className="navbar__list-items"><Link activeClass="active" isDynamic={true} to="skill" spy={true} smooth={true} offset={50} duration={500}>Skill</Link></div>
                <div className="navbar__list-items"><Link activeClass="active" isDynamic={true} to="project" spy={true} smooth={true} offset={50} duration={500}>Works</Link></div>
                <div className="navbar__list-items"><Link activeClass="active" isDynamic={true} to="gallery" spy={true} smooth={true} offset={50} duration={500}>Gallery</Link></div>
            </div>
            {/* <div className="navbar__toggle">

            </div> */}
        </div>
    );
}

export default NavbarTop;
