import React from 'react';
import './gallery.scss';

import { Splide, SplideSlide } from '@splidejs/react-splide';
import '@splidejs/splide/dist/css/themes/splide-default.min.css';

import { SRLWrapper } from 'simple-react-lightbox'

import Image1 from '../img/Mobile/IMG_20200616_221635(1).jpg'
import Image2 from '../img/Mobile/IMG_20200817_172354.jpg'
import Image3 from '../img/Mobile/IMG_20200817_161820.jpg'
import Image4 from '../img/Mobile/IMG_20200808_141233.jpg'
import Image5 from '../img/Mobile/IMG_20200817_144254.jpg'
import Image6 from '../img/Mobile/IMG_20200410_205652(1).jpg'
import Image7 from '../img/Mobile/IMG_20200907_205658_795.jpg'
import Image8 from '../img/Mobile/IMG_20200921_173036.jpg'
import Image9 from '../img/Mobile/IMG_20200616_221659.jpg'
import Image10 from '../img/Mobile/IMG_20200817_165054.jpg'

const gallery = [
    {
        img: Image1,
        title: "Mobile Photography"
    },
    {
        img: Image2,
        title: "Mobile Photography"
    },
    {
        img: Image3,
        title: "Mobile Photography"
    },
    {
        img: Image4,
        title: "Mobile Photography"
    },
    {
        img: Image6,
        title: "Mobile Photography"
    },
    {
        img: Image7,
        title: "Flat Design"
    },
    {
        img: Image5,
        title: "Mobile Photography"
    },
    {
        img: Image9,
        title: "Mobile Photography"
    },
    {
        img: Image10,
        title: "Mobile Photography"
    }
]

function Gallery() {

    const option = {
        rewind: true,
        perPage: 6,
        perMove: 1,
    }

    const srl = {
        settings: {
            disableKeyboardControls: true,
            disablePanzoom: true,
            disableWheelControls: true,
        },
        buttons: {
            showAutoplayButton: false,
            showCloseButton: false,
            showDownloadButton: false,
            showFullscreenButton: false,
            showNextButton: false,
            showPrevButton: false,
            showThumbnailsButton: false,
        }, thumbnails: {
            showThumbnails: false,
        }, caption: {
            showCaption: true,
            captionFontFamily: 'Poppins',
            captionTextTransform: 'uppercase',
        }
    }

    return (
        <div className="gallery">
            <div className="gallery__container">
                <div className="gallery__container-content">
                    <div className="gallery__container-title">
                        <h1> <span>where i </span> live <span>now ?? </span> </h1>
                    </div>
                    <Splide className="gallery__container-gallery" options={option}>
                        {gallery.map((items) => {
                            return (
                                <SplideSlide>
                                    <SRLWrapper options={srl}>
                                        <div className="box">
                                            <img src={items.img} alt={items.title} />
                                        </div>
                                    </SRLWrapper>
                                </SplideSlide>
                            );
                        })}
                    </Splide>
                </div>
            </div>
        </div>
    );
}

export default Gallery;
